<script>
export default {
    name:'PKeyboard',
    data(){
        return {
            glide:false
        }
    },
    props:{
        octave:{
            type:String,
            required:true
        }
    },
    mounted(){
        const pianoKeys=this.$el.getElementsByTagName("path")
        pianoKeys.forEach(elem => {
            elem.addEventListener("mousedown",ev=>{
                // console.log(ev)
                this.glide=true
                ev.target.style.fill="var(--key-down-color)"
                this.$emit("notedown",ev.target.getAttribute("pitch")+this.octave)
            })
            elem.addEventListener("mouseup",ev=>{
                // console.log(ev)
                this.glide=false
                ev.target.style.fill=""
                this.$emit("noteup",ev.target.getAttribute("pitch")+this.octave)
            })
            elem.addEventListener("mouseenter",ev=>{
                if(this.glide){
                    ev.target.style.fill="var(--key-down-color)"
                    this.$emit("notedown",ev.target.getAttribute("pitch")+this.octave)
                }
            })
            elem.addEventListener("mouseleave",ev=>{
                ev.target.style.fill=""
                this.$emit("noteup",ev.target.getAttribute("pitch")+this.octave)
            })
        })
        document.addEventListener("mouseup",()=>{
            this.glide=false
        })
        document.addEventListener("mousedown",()=>{
            this.glide=true
        })
    },
}
</script>

<style>
.p-keyboard{
    display: inline-block;
    margin: 0 0.8px;
    height: 200px;
    --white-key-color: #d72;
    --black-key-color: #444;
    --key-down-color : #853;
}
.p-keyboard svg{
    height: 200px;
}
.p-keyboard .white-key{
    fill: var(--white-key-color);
}
.p-keyboard .black-key{
    fill: var(--black-key-color);
}
.p-keyboard .key-down{
    fill: var(--key-down-color);
}
</style>